import { expose } from 'comlink';
import { AppVersion } from '../../../store/state/application-version.state';


export class VersionPollingWorker {

	private currentInterval: number;

	initPolling(currentHash: string): Promise<AppVersion> {
		return new Promise((resolve) => {
			if (this.currentInterval) {
				clearInterval(this.currentInterval);
			}

			// @ts-ignore
			this.currentInterval = setInterval(async () => {
				const latestVersion = await this.getNewVersion();
				if (currentHash !== latestVersion.hash) {
					resolve(latestVersion);
					clearInterval(this.currentInterval);
				}
			}, 10000);
		});
	}

	async getNewVersion(): Promise<AppVersion> {
		const response = await fetch('/version.json?' + Date.now());
		return response.json();
	}
}

expose(VersionPollingWorker);
